import MarkdownIt from "markdown-it"

export default class GameButton {
  private _label!: string
  public fun: () => any

  constructor(label: string, fun: () => any) {
    this.label = label
    this.fun = fun
  }

  setLabel(label: string): GameButton {
    this.label = label
    return this
  }

  set label(value: string) {
    let md = new MarkdownIt()
    this._label = md.render(value)
  }

  get label(): string {
    return this._label
  }
}
