import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Card from "@/cards/Card"
import Dice from "@/Dice"
import ValueMod from "@/effects/ValueMod"
import Deck from "@/cards/Deck"
import PickStage from "@/stages/PickStage"
import GameButton from "@/GameButton"
import util from "@/util"
import BonusStage from "@/stages/BonusStage"
import EndStage from "@/stages/EndStage"
import ChatMessage from "@/ChatMessage"
import DialogueTree from "@/DialogueTree"

export default class TransformStage extends GameStage {
  constructor(game: Game) {
    super(game, "transform")
  }

  onStart() {
    this._nextStage = this.args.nextStage

    if (this.game.pickedCards.size() > 0) {
      this.buttons = [new GameButton(`Activate **${this.nextCard.fullTitle}**`, () => this.activateCard(this.nextCard))]
    } else {
      this.buttons = [this.nextButton]
    }

    this.game.diceCount = 0
    this.game.drawnCards = new Deck([])
  }

  onEnd() {
    this.game.pickedCards = new Deck()
  }

  private get nextCard(): Card {
    return this.game.pickedCards.cards[0]
  }

  activateCard(card: Card) {
    this.game.pickedCards.removeCard(card)
    this.game.drawnCards.addCard(card)
    this.game.player.hand.addCard(card)
    this.createNextLinearSegment(card)
  }

  createNextLinearSegment(card: Card) {
    this.endpoints.nextTransformation = () => {
      if (this.nextCard != undefined) {
        this.activateCard(this.nextCard)
      }
    }

    this.endpoints.nextStage = () => {
      this.nextStage()
    }

    let messages = []
    if (card.transformation != "") {
      messages = ChatMessage.parseLines(card.transformation)
    } else {
      messages = ChatMessage.parseLines(`You don't feel any different.`)
    }

    if (this.nextCard == undefined) {
      let label = "Next Round"
      if (this.game.round.fraction == 1) {
        if (this.game.state.bonusRolls.value > 0 && !this.game.state.isBonusRound) {
          label = "To Bonus Round"
        } else {
          label = "To Epilogue"
        }
      }

      DialogueTree.createLinear(this, messages,
        "nextStage", label)
    } else {
      DialogueTree.createLinear(this, messages,
        "nextTransformation", `Activate **${this.nextCard.fullTitle}**`)
    }
  }

}
