import { reactive, toRaw } from 'vue'
import Deck from "@/cards/Deck"
import globalState from "@/store/globalState"
import Dice from "@/Dice"
import Card from "@/cards/Card"
import GameStage from "@/stages/GameStage"
import TextStage from "@/stages/TextStage"
import PickStage from "@/stages/PickStage"
import DrawStage from "@/stages/DrawStage"
import ValueRange from "@/effects/ValueRange"
import DialogueScreen from "@/components/screens/DialogueScreen.vue"
import GameButton from "@/GameButton"
import Menu from "@/Menu"
import MarkdownIt from "markdown-it"
import Player from "@/Player"
import GameMode from "@/GameMode"
import StoryMode from "@/StoryMode"
import ChatMessage from "@/ChatMessage"

export default class Game {
  // State variables are all given default values in restart function
  state = reactive({
    deck: new Deck(),
    dice: [] as Dice[],
    drawnCards: new Deck([]) as Deck,
    pickedCards: new Deck([]) as Deck,
    stage: new TextStage(this) as GameStage,
    round: new ValueRange(0, 0, 3),
    isBonusRound: false,
    cardsToPick: 0,
    cardsPickedThisRound: 0,
    rewardRolls: new ValueRange(0, 0, 8),
    bonusRolls: new ValueRange(0, 0, 8),
    player: new Player(),
    lines: [] as ChatMessage[]
  })

  public dialogue: any
  public dialogue2: any
  public menu: Menu

  private isPrinting: boolean
  private printQueue: ChatMessage[]
  private gameMode: GameMode

  public static maxDice: number = 5

  constructor(menu: Menu, gameMode = undefined) {
    this.menu = menu
    this.dialogue = JSON.parse(globalState().dialogueString.value)
    this.dialogue2 = JSON.parse(globalState().dialogue2String.value)

    this.isPrinting = false
    this.printQueue = []

    // Reset state variables
    this.state.deck = new Deck().createNew().pack()
    this.state.dice = []
    this.state.drawnCards = new Deck([])
    this.state.pickedCards = new Deck([])
    this.state.round = new ValueRange(0, 0, 3)
    this.state.isBonusRound = false
    this.state.cardsToPick = 0
    this.state.cardsPickedThisRound = 0
    this.state.rewardRolls = new ValueRange(0, 0, 8)
    this.state.bonusRolls = new ValueRange(0, 0, 8)
    this.state.lines = []

    this.gameMode = (gameMode != undefined) ? gameMode! : new StoryMode(this)
    this.state.stage = this.gameMode.firstStage

    this.state.deck.shuffle()
    this.lockDice(true)
    this.gameMode.start()
    console.log("Deck size:", this.state.deck.size(), this.state.deck.allLevels.length)

    /** Replaces currently drawn cards. */
    // @ts-ignore
    window.gredraw = (value: number) => {
      if (!(this.state.stage.id == 'draw' || this.state.stage.id == 'pick' || this.state.stage.id == 'bonus')) {
        return `Can not redraw cards in this stage.`
      }
      let cards = this.drawCards(value)
      this.state.drawnCards = cards
      if (this.state.stage.id == 'pick') (this.state.stage as PickStage).deck = cards
      return `Drew ${value} cards, ${this.deck.size()} cards remain in the current deck.`
    }

    // @ts-ignore
    window.groll = (value: number) => {
      this.state.stage.onDiceRoll([value])
    }

    // @ts-ignore
    window.gameComponent = () => {
      return this.activeComponent
    }
  }

  end() {
    this.menu.endGame()
  }

  /** Draws cards from this games current deck using correct preferences. */
  drawCards(amount: number): Deck {
    return this.state.deck.draw(amount)
  }

  /** Adds a card to the players hand */
  pickCard(card: Card) {
    this.state.pickedCards.addCard(card)
    this.state.cardsPickedThisRound += 1
  }

  nextStage() {
    this.state.stage.nextStage()
  }

  get buttons(): GameButton[] {
    return this.state.stage.buttons as GameButton[]
  }

  setStage(stage: GameStage) {
    this.state.stage = stage
  }

  currentRolls: number[] = []
  notifyDiceRoll(value: number) {
    this.currentRolls.push(value)

    if (this.allDiceReady) {
      this.state.stage.onDiceRoll(this.diceValues)
      this.currentRolls = []

      for (let die of this.state.dice) {
        die.useValue()
      }
    }
  }

  get diceValues(): number[] {
    let values = []
    for (let die of this.state.dice) {
      values.push(die.value)
    }
    return values
  }

  get allDiceReady(): boolean {
    return this.diceReadyCount == this.state.dice.length
  }

  get diceReadyCount(): number {
    let ready = 0
    for (let die of this.state.dice) {
      if (!die.isUsed) ready += 1
    }
    return ready
  }

  get diceRollingCount(): number {
    let rollCount = 0
    for (let die of this.state.dice) {
      if (die.rolling) rollCount += 1
    }
    return rollCount
  }

  lockDice(value: boolean) {
    for (let die of this.state.dice) {
      die.locked = value
    }
  }

  diceAreLocked(): boolean {
    let locked = 0
    for (let die of this.state.dice) {
      if (die.locked) locked += 1
    }
    return locked == this.state.dice.length
  }

  buyDice() {
    if (!this.allowDiceBuy()) return
    this.bonusRolls.addValue(1)

    if (this.state.dice.length == 0) this.state.dice.push(new Dice(6, this))
    else this.state.dice.push(new Dice(this.state.dice[0].max, this))
  }

  allowDiceBuy(): boolean {
    return this.state.stage.allowDiceBuy() &&
      this.diceReadyCount + this.diceRollingCount == 0 &&
      !this.diceAreLocked() &&
      this.state.dice.length < Game.maxDice &&
      this.state.bonusRolls.value < this.state.bonusRolls.max
  }

  get activeComponent(): string {
    return this.state.stage.activeComponent
  }

  set diceCount(value: number) {
    let dice: Dice[] = []
    for (let i = 0; i < value; i++) {
      if (this.state.dice.length == 0) dice.push(new Dice(6, this))
      else dice.push(new Dice(this.state.dice[0].max, this))
    }
    this.state.dice = dice
  }

  set diceSize(value: number) {
    for (let i = 0; i < this.state.dice.length; i++) {
      this.state.dice[i] = new Dice(value, this)
    }
  }

  clickCard(card: Card) {
    this.state.stage.onCardClick(card)
  }

  get player(): Player {
    return this.state.player as Player
  }

  sendMessage(msg: ChatMessage) {
    this.state.lines.push(msg)
  }

  print(text: string) {
    let lines = text.split("\n").filter(line => line.length > 0)
    for (let line of lines) {
      let msg = ChatMessage.parseLine(line)
      this.printQueue.push(msg)
    }
    this.startPrinter(this.printQueue)
  }

  private startPrinter(queue: ChatMessage[]) {
    if (this.isPrinting) return

    this.printer(queue)
  }

  private printer(queue: ChatMessage[]) {
    if (queue.length == 0) {
      this.isPrinting = false
      return
    }

    this.isPrinting = true

    let msg: ChatMessage = queue.splice(0, 1)[0]
    this.state.lines.push(msg)

    if (queue.length > 0) {
      let next = queue[0]
      let timeout = 0// Math.max(50 * next.text.split(' ').length, 400)
      console.log(timeout)
      setTimeout(() => this.printer(queue), timeout)
    } else {
      this.isPrinting = false
    }
  }

  get currentDialogue(): ChatMessage[] {
    //let lines = this.state.text.split("\n").filter(line => line.length > 0)
    //return lines.map(l => ChatMessage.parseLine(l))
    return this.state.lines
  }

  get deck(): Deck {
    return this.state.deck as Deck
  }

  get drawnCards(): Deck {
    return this.state.drawnCards as Deck
  }

  set drawnCards(value: Deck) {
    this.state.drawnCards = value
  }

  get pickedCards(): Deck {
    return this.state.pickedCards as Deck
  }

  set pickedCards(value: Deck) {
    this.state.pickedCards = value
  }

  get round(): ValueRange {
    return this.state.round as ValueRange
  }

  get roundString(): string {
    if (this.state.isBonusRound) return "Bonus Round"
    return `Round ${this.round.value} of ${this.round.max}`
  }

  get stageId(): string {
    return this.state.stage.id
  }

  get rewardRolls(): ValueRange {
    return this.state.rewardRolls as ValueRange
  }

  get bonusRolls(): ValueRange {
    return this.state.bonusRolls as ValueRange
  }

  get cardsPickedThisRound(): number {
    return this.state.cardsPickedThisRound
  }

  get showDialogueOnly(): boolean {
    return this.round.value == 0
  }
}
