import Game from "@/Game"
import Card from "@/cards/Card"
import GameButton from "@/GameButton"
import { reactive } from "vue"
import ChatMessage from "@/ChatMessage"

export default abstract class GameStage {
  public id: string = "stage"
  protected game: Game
  protected args: any = {}
  private _text: string = ""
  protected _nextStage: GameStage | undefined
  protected _allowDiceBuy: boolean = false
  public activeComponent: string = "GameScreen"

  public endpoints: { [key: string]: () => any } = {}

  private state: any = reactive({
    buttons: []
  })

  protected nextButton: GameButton = new GameButton("Next", () => this.nextStage())

  protected constructor(game: Game, id: string) {
    this.game = game
    this.id = id
  }

  onStart() {}

  onResume() {}

  onEnd() {}

  onDiceRoll(values: number[]) {}

  onCardClick(card: Card) {}

  link(stage: GameStage): GameStage {
    this._nextStage = stage
    return stage
  }

  resumeStage(stage: GameStage) {
    this.game.setStage(stage)
    stage.onResume()
  }

  nextStage(stage: GameStage | undefined = undefined, args: any = undefined) {
    this.onEnd()
    if (stage != undefined) {
      this.gotoNext(stage, args)
    } else if (this._nextStage != undefined) {
      this.gotoNext(this._nextStage, args)
    } else {
      this.game.setStage(this)
    }
  }

  allowDiceBuy(): boolean {
    return this._allowDiceBuy
  }

  get text() {
    return this._text
  }

  set text(value: string) {
    this._text = value
    this.game.print(value)
  }

  createMessage(text: string) {
    this.sendMessage(ChatMessage.parseLine(text))
  }

  sendMessage(msg: ChatMessage) {
    this.game.sendMessage(msg)
  }

  private gotoNext(stage: GameStage, args: any = undefined) {
    this.game.setStage(stage)
    stage.args = args
    stage.onStart()
  }

  get buttons(): GameButton[] {
    return this.state.buttons
  }

  set buttons(value: GameButton[]) {
    this.state.buttons = value
  }
}
