
import { Options, Vue } from 'vue-class-component'
import Game from '@/Game'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/cards/Card'
import DiceView from "@/components/DiceView.vue"
import Menu from "@/Menu"
import DialogueStatusCard from "@/components/DialogueStatusCard.vue"
import CardHandWindow from "@/components/CardHandWindow.vue"
import Deck from "@/cards/Deck"
import DialogueCard from "@/components/DialogueCard.vue"

@Options({
  components: {
    TextButton, PlayingCard, DiceView, DialogueCard, DialogueStatusCard, CardHandWindow
  },
  props: {
    menu: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    }
  },
  computed: {
    game: function (): Game {
      return this.menu.game
    },
    handDeck: function (): Deck {
      if (this.showActivatedCards) return this.game.player.hand.copy().addDeck(this.game.pickedCards)
      return this.game.pickedCards
    }
  }
})
export default class GameScreen extends Vue {
  menu!: Menu
  scaledCard: Card = new Card({})
  showActivatedCards: boolean = false
}
