import MarkdownIt from "markdown-it"

export default class ChatMessage {
  name: string
  text: string

  constructor(name: string = "", text: string = "") {
    this.name = name
    this.text = text
  }

  static parseLine(line: string): ChatMessage {
    let md = new MarkdownIt({ breaks: true })
    let name = "narrator"
    let text = line

    if (line.startsWith("demon: ")) {
      name = "demon"
      text = line.replace("demon: ", "")
    }
    if (line.startsWith("player: ")) {
      name = "player"
      text = line.replace("player: ", "")
    }

    return new ChatMessage(name, text.split("\n").map(t => md.render(t)).join("\n"))
  }

  static parseLines(text: string): ChatMessage[] {
    let messages = text.split(/\n\n/)
      .filter((line: string) => { return line.length > 0 })
      .map((line: string) => ChatMessage.parseLine(line))

    return messages
  }
}
