import util from "@/util"
import Card from "@/cards/Card"
import { custom, deserialize, getDefaultModelSchema, list, object, serializable } from "serializr"
import globalState from "@/store/globalState"
import Family from "@/cards/Family"
import { toRaw } from "vue"
import DrawPreference from "@/cards/DrawPreference"
import FilterSetting from "@/FilterSetting"

export default class Deck {
  public cards: Card[]

  public static filters: FilterSetting[] = []
  private static cardsLoaded = false
  private static _cardMap: {[key: string]: Card} = {}
  private static _referenceDeck: Deck

  constructor(cards: Card[] | undefined = undefined) {
    if (cards != undefined) {
      this.cards = cards
    } else {
      this.cards = []
    }
  }

  createNew(): Deck {
    return Deck.referenceDeck().copy().useFilters()
  }

  /** Returns a new deck with this one's cards, but with all cards of a given family removed. */
  withoutFamily(family: string): Deck {
    let cards: Card[] = []
    for (let card of this.cards) {
      if (card.family.id != family) cards.push(card)
    }
    return new Deck(cards)
  }

  /** Returns a new deck with this one's cards, but only with cards of a given family.
   *
   * Using 'all' will return a copy of this entire deck.
   * */
  onlyFamily(family: string): Deck {
    if (family == 'all') return new Deck(this.cards)

    let cards: Card[] = []
    for (let card of this.cards) {
      if (card.family.id == family) cards.push(card)
    }
    return new Deck(cards)
  }

  useFilters(): Deck {
    return this.filterBy(Deck.filters)
  }

  private filterBy(filters: FilterSetting[]): Deck {
    let cards: Card[] = []
    for (let card of this.cards) {
      let addCard: boolean = true

      // Filter subcards
      if (card.isParent) {
        for (let subcard of card.subcards) {
          for (let filter of filters) {
            if (subcard.hasTag(filter.tag) && filter.strictness == 2) {
              card.subcards = card.subcards.filter(c => c.id != subcard.id)
            }
            if (subcard.hasTag(filter.tag) && subcard.hasTag('extreme') && filter.strictness == 1) {
              card.subcards = card.subcards.filter(c => c.id != subcard.id)
            }
          }
        }
      }

      // Filter this card
      for (let filter of filters) {
        if (card.hasTag(filter.tag)) {
          if (filter.strictness == 2) {
            addCard = false
            break
          }
          if (filter.strictness == 1 && card.hasTag("extreme")) {
            addCard = false
            break
          }
        }
      }

      if (addCard) cards.push(card)
    }
    return new Deck(cards)
  }

  /** Makes all cards in this deck references to cards in a single common reference deck. */
  rebase(): Deck {
    let deck: Deck = new Deck()
    for (let card of this.cards) {
      deck.addCard(Deck.lookup(card.id)!)
    }
    return deck
  }

  /** Returns this deck but with each card only appearing once. */
  unique(): Deck {
    let deck: Deck = new Deck()
    for (let card of this.cards) {
      if (deck.getCardById(card.id) == undefined) {
        deck.addCard(card)
      }
    }

    return deck
  }

  /** Returns this deck but packs child cards into their parents as sub-cards.*/
  pack(): Deck {
    let deck: Deck = new Deck()
    for (let card of this.cards) {
      if (card.hasParent) {
        let parentId = card.parent!.id
        let parent: Card

        if (deck.hasCard(parentId)) {
          // If the parent already exists in the new deck
          parent = deck.getCardById(parentId)!

        } else if (this.hasCard(parentId)) {
          // If the parent exists in the old deck
          parent = this.getCardById(parentId)!
          deck.addCard(parent)

        } else {
          // If the parent doesn't exist in either deck, create it from static
          parent = Deck.lookup(parentId)!
          parent.subcards = []
          deck.addCard(parent)
        }
        parent.addSubCard(card)

      } else {
        deck.addCard(card)
      }
    }
    return deck
  }

  /** Returns this deck but with all child cards directly in the deck. All parent cards are removed. */
  unpack(): Deck {
    let cards: Card[] = []
    for (let card of this.cards) {
      if (card.isParent) {
        for (let i = 0; i < card.subcards.length; i++) {
          cards.push(card.getLevel(i))
        }
      } else {
        cards.push(card)
      }
    }
    return new Deck(cards)
  }

  /**
   * Returns the cards on 1-based indices specified by values.
   * @param values
   */
  pick(values: number[]): Deck {
    let cards: Card[] = []
    for (let i = 0; i < this.cards.length; i++) {
      for (let value of values) {
        if (i == value - 1) cards.push(this.cards[i])
      }
    }
    return new Deck(cards)
  }

  /** Draws a number of cards from the top of the deck. */
  /*draw(value: number): Deck {
    return this.draw2(value, { tags: ["mind", "pet"], prob: 0.5 })
    let cards: Card[] = []
    for (let i = 0; i < value; i++) {
      if (!this.drawOneCardTo(cards)) break
    }
    return new Deck(cards)
  }*/

  // {tags: ["mind", "pet"], prob: 0.5}
  draw(value: number, preferred: DrawPreference = new DrawPreference([], 0, 0)): Deck {
    // Assume that index 0 is bottom of deck, highest index is top
    let wantedCards: Card[] = this.getWantedCards(preferred.tags)
    let cards: Card[] = []

    while (cards.length < value && cards.length < preferred.guaranteed && wantedCards.length > 0) {
      let card = util.pickFrom(wantedCards, Math.random())

      // Remove that card from both wanted list and this deck
      let wantedIndex = wantedCards.findIndex((c: Card) => c.id == card.id)
      if (wantedIndex != -1) wantedCards.splice(wantedIndex, 1)
      this.removeCard(card)

      cards.push(card)
      console.log("Guaranteed", card.name)
    }

    while (cards.length < value) {
      // Draw a wanted card with certain chance, otherwise just draw top card
      if (wantedCards.length > 0 && Math.random() < preferred.probability) {
        // Remove the topmost wanted card
        let card = wantedCards.pop()!
        // Remove that card from deck as well
        this.cards.splice(this.cards.findIndex((c: Card) => c.id == card.id), 1)
        cards.push(card)
      } else if (this.cards.length > 0) {
        // Remove the topmost card in the deck
        let card = this.cards.pop()!
        // If that card is wanted, remove it from the wanted list
        let wantedIndex = wantedCards.findIndex((c: Card) => c.id == card.id)
        if (wantedIndex != -1) wantedCards.splice(wantedIndex, 1)

        cards.push(card)
      } else {
        break
      }
    }
    return new Deck(cards).shuffle()
  }

  private getWantedCards(tags: string[]): Card[] {
    // Assume that index 0 is bottom of deck, highest index is top
    let wantedCards: Card[] = []
    for (let card of this.cards) {
      if (card.hasAnyTag(tags)) wantedCards.push(card)
    }
    return wantedCards
  }

  /** Draws a card and adds it to the list. Returns false if no card could be drawn. */
  private drawOneCardTo(cards: Card[]): boolean {
    let card: Card | undefined = this.cards.pop()
    if (card != undefined) {
      cards.push(card)
      return true
    }
    return false
  }

  /** Draws the card with given index. */
  private drawIndex(index: number) {
    return this.cards.splice(index, 1)[0]
  }

  addCard(card: Card): Deck {
    this.cards.push(card)
    return this
  }

  addDeck(deck: Deck): Deck {
    this.cards = this.cards.concat(deck.cards)
    return this
  }

  removeCard(card: Card): Deck {
    let i = this.cards.findIndex(c => c.id == card.id)
    if (i != -1) this.drawIndex(i)
    return this
  }

  hasCard(cardId: string): boolean {
    return this.cards.find(card => card.id == cardId) != undefined
  }

  size(): number {
    return this.cards.length
  }

  shuffle(): Deck {
    this.cards = util.shuffle(this.cards)
    return this
  }

  sortByFamily() {
    this.cards = this.cards.sort((a, b) => (a.family.id < b.family.id) ? -1 : 1)
    return this
  }

  sortByLevel(): Deck {
    this.cards = this.cards.sort((a, b) => a.level - b.level)
    return this
  }

  sortByName(): Deck {
    this.cards = this.cards.sort((a, b) => (a.name < b.name) ? 1 : -1)
    return this
  }

  removeAllCards(): Deck {
    this.cards = []
    return this
  }

  get pickableCards(): Card[] {
    let cards: Card[] = []
    for (let card of this.cards) {
      if (!card.isParent) cards.push(card)
      for (let i = 0; i < card.subcards.length; i++) {
        cards.push(card.getLevel(i))
      }
    }
    return cards
  }

  /** Returns all cards, not just top level ones. */
  get allLevels(): Card[] {
    let cards = []
    for (let card of this.cards) {
      cards.push(card)
      if (card.isParent) {
        for (let sub of card.subcards) {
          cards.push(sub)
        }
      }
    }
    return cards
  }

  getCardById(id: string): Card | undefined {
    let card = this.allLevels.find(card => card.id == id)
    return card
  }

  copy(): Deck {
    return new Deck(this.cards.map((c: Card) => c.copy()))
  }

  public static createFromCardStates(cardStates: any[]): Deck {
    let cards: Card[] = []
    for (let state of cardStates) {
      let card: Card | undefined = Deck.lookup(state.id)
      if (card != undefined) {
        cards.push(card)
      } else {
      }
    }
    return new Deck(cards)
  }

  public static lookup(id: string): Card | undefined {
    let card = this.cardMap()[id]
    if (card == undefined) {
      console.warn(`Static lookup of card "${id}" failed.`)
      return undefined
    }
    return card.copy()
  }

  public static referenceDeck(): Deck {
    if (this._referenceDeck != undefined) {
      return this._referenceDeck.copy()
    }

    let cards = this.parseCards()

    let deck: Deck = new Deck()
    for (let card of cards) {
      if (!card.isParent) deck.addCard(card)
    }

    this._referenceDeck = deck.pack()
    return this._referenceDeck.copy()
  }

  private static cardMap(): {[key: string]: Card} {
    if (this.cardsLoaded) return this._cardMap

    for (let card of this.parseCards()) {
      this._cardMap[card.id] = card
    }

    this.cardsLoaded = true
    return this._cardMap
  }

  private static parseCards(): Card[] {
    let families: any = JSON.parse(globalState().cardFamilyString.value)
    let cards = []
    for (let familyState of families) {
      let family = new Family(familyState)
      for (let card of familyState.cards) {
        let levels: Card[] = []

        // If this card is a parent
        if (card.levels != undefined) {
          // Create all level cards for it
          for (let [i, subcard] of card.levels.entries()) {
            let tags = (card.tags) ? card.tags : []
            if (subcard.tags) tags = tags.concat(subcard.tags)

            let leveledCard: Card = new Card({
              family: family.id,
              thumb: (subcard.thumb) ? `cards/${subcard.thumb}` : (card.thumb) ? `cards/${card.thumb}` : `cards/${family.thumb}`,
              id: `${card.id}.${subcard.id}`,
              name: subcard.name,
              description: subcard.description,
              transformation: (subcard.transformation) ? subcard.transformation : (card.transformation) ? card.transformation : "",
              type: card.type,
              subcards: [],
              levels: 1,
              level: i + 1,
              parent: card.id,
              hue: family.hue,
              artist: (subcard.artist) ? subcard.artist : (card.artist) ? card.artist : (family.artist) ? family.artist : "Gardamuse",
              tags: tags
            })
            levels.push(leveledCard)
            cards.push(leveledCard)
          }
        }
        cards.push(this.parseCard(card, family, levels))
      }
    }

    return cards
  }

  /** Parses a single card. */
  private static parseCard(card: any, family: any, subcards: Card[] = []) {
    return new Card({
      family: family.id,
      thumb: (card.thumb) ? `cards/${card.thumb}` : `cards/${family.thumb}`,
      id: card.id,
      name: card.name,
      description: card.description,
      transformation: card.transformation,
      type: card.type,
      subcards: subcards.map(c => c.id),
      levels: subcards.length,
      hue: family.hue,
      artist: (card.artist) ? card.artist : (family.artist) ? family.artist : "Gardamuse",
      tags: (card.tags) ? card.tags : []
    })
  }
}
