import { serializable } from "serializr"

export default class FilterSetting {
  @serializable
  public readonly name: string
  @serializable
  public readonly description: string
  @serializable
  public readonly tag: string
  @serializable
  private _strictness: number = 0

  constructor(tag: string, name: string, description: string) {
    this.tag = tag
    this.name = name
    this.description = description
  }

  load(state: any) {
    if (state == undefined) return

    this._strictness = state._strictness
  }

  get strictness(): number {
    return this._strictness
  }

  set strictness(value: number) {
    this._strictness = value
  }
}
