import Save from "@/Save"
import { deserialize } from "serializr"

export default class SaveSystem {
  private saves: {[key: string]: any} // Working copy of all saves. This is a map from save name to save object.

  constructor() {
    this.saves = this.loadAllFromStorage()
  }

  public save(name: string, version: string, state: any) {
    let save: Save = new Save(name, version, state)
    this.writeToLocalStorage(save)
  }

  /**
   * Returns the save, or undefined if no save with that name was found.
   * @param name
   */
  public load(name: string): any {
    if (name in this.saves) return this.readFromLocalStorage(name)
    return undefined
  }

  private deleteFromLocalStorage(name: string) {
    delete this.saves[name]
    localStorage.setItem("blushingdefeat-saves", JSON.stringify(this.saves))
  }

  private writeToLocalStorage(save: Save) {
    this.saves[save.name] = save
    localStorage.setItem("blushingdefeat-saves", JSON.stringify(this.saves))
  }

  private readFromLocalStorage(name: string): Save {
    return deserialize(Save, this.saves[name]) as unknown as Save
  }

  private loadAllFromStorage(): {[key: string]: any} {
    let savesString = localStorage.getItem("blushingdefeat-saves")
    let saves: {[key: string]: any} = {}
    if (savesString != null) {
      saves = JSON.parse(savesString)
    }
    return saves
  }
}
