
import { Options, Vue } from 'vue-class-component'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'
import Card from "../cards/Card"
import util from "@/util"

@Options({
  components: {
    ValueMeter, TextButton
  },
  props: {
    card: Object
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    }
  },
  computed: {
    cssVars() {
      return {
        '--border-color': 'black',
        '--background-hue': this.card.hue,
        '--background-image': 'url(' + require('@/assets/black-felt.png') + ')',
        '--thumb': 'url(' + util.baseUrl() + '/' + this.card.thumb + ')'
      }
    }
  }
})
export default class PlayingCard extends Vue {
  card!: Card
}
