import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import GameButton from "@/GameButton"

export default class TextStage extends GameStage {
  private myText: string

  constructor(game: Game, text: string = "") {
    super(game, "text")
    this.myText = text
    this.buttons = [this.nextButton]
  }

  onStart() {
    this.text = this.myText
  }
}
