import GameStage from "@/stages/GameStage"
import ChatMessage from "@/ChatMessage"

export default class DialogueNode {
  private stage: GameStage

  public readonly id: { id: string; index: number }

  /** If this node is selectable, title may be used to represent it on a button. */
  public readonly title: string

  /** The message content of this node. */
  private _msg: ChatMessage

  private _options: DialogueNode[]

  public endpoint: string = ""

  constructor(id: { id: string; index: number }, stage: GameStage, msg: ChatMessage, title: string = "") {
    this.stage = stage
    this.id = id
    this.title = title
    this._msg = msg
    this._options = []
  }

  get msg(): ChatMessage {
    return this._msg
  }

  addOption(option: DialogueNode): DialogueNode {
    this._options.push(option)
    return this
  }

  get options(): DialogueNode[] {
    return this._options
  }

}
