<template>
  <div class="card-hand-window cards-wrapper drawn-cards">
    <div class="label-wrapper">
      <span class="label-row"><slot></slot></span>
    </div>
    <div class="cards">
      <template v-for="card in deck.cards" v-bind:key="card">
        <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="toggleScale(card); game.clickCard(card);"></PlayingCard>
      </template>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Game from '@/Game'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/cards/Card'
import DiceView from "@/components/DiceView.vue"
import Menu from "@/Menu"
import DialogueStatusCard from "@/components/DialogueStatusCard.vue"
import Deck from "@/cards/Deck"

@Options({
  components: {
    PlayingCard
  },
  props: {
    menu: Object,
    deck: Object,
    label: String
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    }
  },
  computed: {
    game: function (): Game {
      return this.menu.game
    }
  }
})
export default class CardHandWindow extends Vue {
  deck!: Deck
  menu!: Menu
  label: string = ""
  scaledCard: Card = new Card({})
}
</script>

<style scoped lang="scss">
  .card-hand-window {
    //z-index: 10;
    background-color: rgba($component-background-color, 0.5);
    position: relative;
  }

  .cards-wrapper {
    overflow-x: auto;
    overflow-y: visible;

    border: 3px solid black;
    border-radius: $border-radius;
  }

  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30px, max-content));
    grid-auto-columns: 30px;
    grid-template-rows: 200px;
    grid-auto-flow: column;
    grid-gap: 10px;
    transform: scale(1);
    transform-origin: top left;

    // The last card sticks out with a width that depends on card count and screen width.
    // This is difficult to calculate, so we just make sure there is always space for one entire card extra.
    width: calc(100% - #{$playing-card-width});
  }

  .scaled {
    transform: scale(1.06);
    z-index: 2;
    border-color: #939393;
  }

  .label-wrapper {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    h1, h2, h3, h4, p {
      margin: 0;
    }
  }

  .label-row {
    display: flex;
  }

  .label {
    padding: 2px 5px;
    border-top-right-radius: $border-radius;
    border-top: 2px solid black;
    border-right: 2px solid black;
    background-color: $window-background-color;

    display: grid;
    grid-auto-flow: column;
    grid-gap: 5px;
