import { list, object, serializable } from "serializr"
import FilterSetting from "@/FilterSetting"

export default class FilterSettings {
  @serializable(list(object(FilterSetting)))
  public filters: FilterSetting[]

  constructor() {
    this.filters = [
      new FilterSetting("expansion", "Expansion", "Changes in size to breasts, butt, lips and similar."),
      new FilterSetting("iq", "Intelligence", "Changes that primarily reduce intelligence."),
      new FilterSetting("doll", "Doll", "Transformations focused on making you seem plastic, fake or doll-like."),
      new FilterSetting("pet", "Pet", "Changes that cause pet-like behavior, inability to walk, having to wear a leash etc."),
      new FilterSetting("animal", "Animal-like", "Changes that give horns, ears, a tail etc. Note that you always remain primarily human."),
      new FilterSetting("lactation", "Lactation", "Changes that cause lactation."),
    ]

    this.filters.sort((a, b) => (a.name < b.name) ? -1 : 1)
  }

  load(state: any) {
    if (state == undefined) return

    for (let filter of state.filters) {
      let myfilter = this.filters.find(f => f.tag == filter.tag)
      if (myfilter == undefined) continue
      myfilter.load(filter)
    }
  }
}
