<template>
  <div class="collection-window">
    <div class="collection-tabs">
      <template v-for="tab in tabs" v-bind:key="tab">
        <TextButton :class="{'active-tab': activeTab === tab}" @click="activateTab(tab)">
          <h3>{{ util.capitalize(tab) }}</h3>
        </TextButton>
      </template>
    </div>
    <div class="cards-border">
      <div class="cards-wrapper">
        <div class="cards" id="cards">
          <template v-for="card in internalDeck.cards" v-bind:key="card">
            <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="activateCard(card);"></PlayingCard>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import TextButton from '@/components/TextButton.vue'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/cards/Card'
import util from "@/util"
import BlushWindow from "@/components/BlushWindow.vue"
import Deck from "@/cards/Deck"
import Menu from "@/Menu"

@Options({
  components: {
    TextButton, PlayingCard, BlushWindow
  },
  props: {
    menu: Object,
    deck: Object,
    groupByParent: Boolean
  },
  emits: ['close', 'activate-tab', 'activate-card'],
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    },
    activateTab(tab: string) {
      this.activeTab = tab
      this.activeCard = new Card({})
      this.$emit('activate-tab', tab)
    },
    activateCard(card: Card) {
      this.activeCard = card
      this.$emit('activate-card', card)
    },
    lookup(id: string): Card {
      return Deck.lookup(id)!
    }
  },
  computed: {
    tabs(): string[] {
      return [
        'all',
        'behavior',
        'body',
        'clothing',
        'event',
        'mind',
        'reward'
      ]
    },
    util() {
      return util
    },
    refDeck(): Deck {
      return Deck.referenceDeck().useFilters()
    },
    internalDeck(): Deck {
      if (this.isInspectingCard) {
        return new Deck(this.activeCard.subcards).sortByLevel().useFilters()
      }
      if (this.groupByParent) {
        return this.deck.copy().onlyFamily(this.activeTab).pack().unique().sortByName().sortByFamily().useFilters()
      }
      return this.deck.copy().onlyFamily(this.activeTab).unpack().unique().sortByName().sortByFamily().useFilters()
    },
    isInspectingCard(): boolean {
      return this.activeCard.id != undefined && this.activeCard.isParent
    }
  }
})
export default class CollectionWindow extends Vue {
  menu!: Menu
  deck!: Deck
  groupByParent: boolean = false
  scaledCard: Card = new Card({})
  activeTab: string = 'all'
  activeCard: Card = new Card({})
}
</script>

