import { reactive, toRefs } from 'vue'
import util from "@/util"
import axios from "axios"
import MarkdownIt from 'markdown-it'

const state = reactive({
  loading: true,
  cardFamilyString: "" as string,
  dialogueString: "" as string,
  dialogue2String: "" as string,
  artistsString: "" as string,
  patreonCredits: "",
  patchNotesString: ""
})

export default function globalState() {
  const load = async () => {
    state.loading = true

    // Await all resources in parallel
    let [familyList, dialogue, dialogue2, artists, credits, patchList] = await Promise.all([
      util.fetchHjson('cards/series.hjson'),
      util.fetchHjson('dialogue.hjson'),
      util.fetchHjson('dialogue-2.hjson'),
      util.fetchHjson('cards/artists.hjson'),
      axios.get('https://www.blushingdefeat.com/assets/blushing-defeat-credits.json'),
      util.fetchHjson('patch-notes/content.hjson')
    ])

    state.dialogueString = JSON.stringify(dialogue)
    state.dialogue2String = JSON.stringify(dialogue2)
    state.artistsString = JSON.stringify(artists)
    state.patreonCredits = credits.data

    // Second round of await for resources specified in the first await
    let familyPromises: any[] = []
    for (let familyPath of familyList) {
      familyPromises.push(util.fetchHjson(`cards/${familyPath}`))
    }
    let families: any[] = await Promise.all(familyPromises)
    state.cardFamilyString = JSON.stringify(families)

    // Load patch notes
    let patches: any[] = []
    let md = new MarkdownIt()
    for (let patchId of patchList) {
      let patch = await util.fetchHjson(`patch-notes/${patchId}.hjson`)
      patch.id = patchId
      patch.text = md.render(patch.text)
      patches.push(patch)
    }
    state.patchNotesString = JSON.stringify(patches)

    state.loading = false
  }

  return { ...toRefs(state), load }
}
