import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Card from "@/cards/Card"
import Dice from "@/Dice"
import ValueMod from "@/effects/ValueMod"
import Deck from "@/cards/Deck"
import PickStage from "@/stages/PickStage"
import GameButton from "@/GameButton"
import util from "@/util"
import BanStage from "@/stages/BanStage"
import ChatMessage from "@/ChatMessage"

export default class DrawStage extends GameStage {
  private allowNext: boolean = true
  private availableExtraDraws: number = 1

  constructor(game: Game) {
    super(game, "draw")
  }

  onStart() {
    this.text = this.game.dialogue['drawStage']
    this.game.state.drawnCards = this.game.drawCards(8)
    this.game.diceSize = Math.min(6, this.game.state.drawnCards.size())
    this.allowNext = false
    this.availableExtraDraws = 1
    this.buttons = []
    this.game.round.addValue(1)
    this.game.state.cardsPickedThisRound = 0
    this.game.state.cardsToPick = 0
    this.game.diceCount = 1
    this._allowDiceBuy = false
    /*this.buttons = [new GameButton("Ban a card", () => {
      super.nextStage(new BanStage(this.game), { drawStage: this })
    })]*/
  }

  onResume() {
    this.text = this.game.dialogue['drawStagePostBan']
    this.game.diceSize = Math.min(6, this.game.state.drawnCards.size())
    this.allowNext = false
    this.availableExtraDraws = 1
    this.buttons = []
    this.game.state.cardsPickedThisRound = 0
    this.game.state.cardsToPick = 0
    this.game.diceCount = 1
    this._allowDiceBuy = false
  }

  onDiceRoll(values: number[]) {
    if (this.allowNext) return
    let value = Math.min(...values)
    this.game.state.cardsToPick = value
    this.createMessage(`demon: You will have to pick ${this.game.state.cardsToPick} cards this round.\nIf you want to pick either more or fewer cards, you can do so, but you also get an extra bonus roll.`)
    this.allowNext = true
    this.updateButtons()
    this.game.lockDice(true)
  }

  nextStage() {
    if (this.allowNext) super.nextStage(new PickStage(this.game), { cardsToPick: this.game.state.cardsToPick, drawStage: this })
  }

  addDraws(value: number) {
    this.game.state.bonusRolls.addValue(1)
    this.game.state.cardsToPick = util.clamp(this.game.state.cardsToPick + value, 1, 6)
    if (value >= 0) {
      this.createMessage(`demon: An extra pick? Guess you're seeing something good, huh? You'll pick ${this.game.state.cardsToPick} cards this round.`)
    } else {
      this.createMessage(`demon: Alright, you only have to pick ${this.game.state.cardsToPick} cards this round. But you do get an extra roll in the bonus round.`)
    }
    this.updateButtons()
  }

  updateButtons() {
    let buttons = []
    if (this.availableExtraDraws > 0) {
      this.availableExtraDraws -= 1
      if (this.game.state.cardsToPick > 1 && this.game.state.bonusRolls.value < this.game.state.bonusRolls.max) {
        buttons.push(new GameButton("Pick 1 less", () => { this.addDraws(-1) }))
      }
      if (this.game.state.cardsToPick < 6 && this.game.state.bonusRolls.value < this.game.state.bonusRolls.max) {
        buttons.push(new GameButton("Pick 1 more", () => { this.addDraws(1) }))
      }
    }
    buttons.push(this.nextButton)
    this.buttons = buttons
  }
}
