import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Dice from "@/Dice"
import Card from "@/cards/Card"
import Deck from "@/cards/Deck"
import IntensityStage from "@/stages/IntensityStage"
import HighlightStage from "@/stages/HighlightStage"
import BonusStage from "@/stages/BonusStage"
import GameButton from "@/GameButton"
import BargainStage from "@/stages/BargainStage"
import TransformStage from "@/stages/TransformStage"
import EndStage from "@/stages/EndStage"

export default class PickStage extends GameStage {
  private leftToPick: number = 0 // How many cards should be picked in this stage
  private diceCount: number = 1
  public deck: Deck = new Deck([])

  constructor(game: Game) {
    super(game, "pick")
  }

  onStart() {
    this.leftToPick = this.args.cardsToPick
    this._nextStage = this.args.drawStage

    this.text = this.game.dialogue['pickStage']
    this.deck = this.game.drawnCards

    this.diceCount = 1
    this.game.diceCount = this.diceCount
    this.game.diceSize = this.deck.size()
    this.buttons = []
    this._allowDiceBuy = true
  }

  onResume() {
    this.game.drawnCards = this.deck
    this.diceCount = Math.max(1, this.diceCount - 1)
    this.game.diceCount = this.diceCount
    this.game.diceSize = this.deck.size()
    this._allowDiceBuy = true
    if (this.leftToPick == 0) {
      this.createMessage(
        `demon: Good job! You completed round ${this.game.round.value}.\n` +
        `Time to see what those cards will do to you!`)
      this.game.lockDice(true)
      //this.buttons = [this.nextButton.setLabel("Next Round")]

      if (this.game.round.fraction == 1) {
        if (this.game.state.bonusRolls.value > 0 && !this.game.state.isBonusRound) {
          this._nextStage = new BonusStage(this.game)
        } else {
          this._nextStage = new EndStage(this.game)
        }
      }

      this.nextStage(new TransformStage(this.game), { nextStage: this._nextStage })
      /*this.buttons = [
        new GameButton(`Activate **${this.game.pickedCards.cards[0].fullTitle}**`,
          () => this.nextStage(new TransformStage(this.game),
            { nextStage: this._nextStage })
        )]*/

      /*this.buttons.push(new GameButton("Devil's Bargain", () => this.nextStage(new BargainStage(this.game), { nextStage: this._nextStage })))*/
    }
  }

  onDiceRoll(values: number[]) {
    if (this.leftToPick > 0) {
      this.leftToPick -= 1
      let highlightedCards = this.deck.pick(values)
      this.game.lockDice(true)
      this.diceCount = values.length

      this.nextStage(new HighlightStage(this.game), { pickStage: this, highlightedCards: highlightedCards })
    }
  }

  /*nextStage(stage: GameStage | undefined = undefined, args: any = undefined) {
    super.nextStage(new TransformStage(this.game), { nextStage: this._nextStage })
  }*/
}
