import Menu from "@/Menu"

export default class Cheat {
  name: string
  description: string
  fun: (menu: Menu, args: any) => any

  constructor(name: string, description: string, cheatFunction: (menu: Menu, args: any) => any) {
    this.name = name
    this.description = description
    this.fun = cheatFunction
  }

  activate(menu: Menu) {
    // @ts-ignore
    window[this.name] = (args: any) => { return this.fun(menu, args) }
  }
}
