import { identifier, list, object, primitive, serializable, serialize } from "serializr"
import Family from "@/cards/Family"
import Deck from "@/cards/Deck"
import { toRaw } from "vue"

export default class Card {

  // Note: All properties must be added both here, in constructor, in copy() and in Deck's parser

  @serializable
  private readonly _family: string
  @serializable
  private readonly _parent: string

  @serializable
  public readonly thumb: string
  @serializable(identifier())
  public readonly id: string
  @serializable
  public readonly name: string
  @serializable
  public readonly description: string
  @serializable
  public readonly transformation: string
  @serializable(list(primitive()))
  private _subcards: string[]
  @serializable
  public readonly type: string // numeric, selection, single
  @serializable
  public readonly _level: number
  @serializable
  public readonly levels: number
  @serializable
  public readonly hue: number
  @serializable
  public readonly artist: string
  @serializable(list(primitive()))
  public readonly tags: string[]

  public scale: boolean = false
  public highlight: boolean = false

  constructor(state: any) {
    if (state == undefined) state = {}

    this._family = state.family ? state.family : "none"
    this._parent = state.parent

    this.thumb = state.thumb
    this.id = state.id
    this.name = state.name
    this.description = state.description
    this.transformation = state.transformation ? state.transformation : ""
    this._subcards = state.subcards ? state.subcards : []
    this._level = state.level ? state.level : 0
    this.levels = state.levels ? state.levels : 0
    this.type = state.type ? state.type : "single"
    this.hue = state.hue ? state.hue : 0
    this.artist = state.artist ? state.artist : "Gardamuse"
    this.tags = state.tags ? state.tags : []
  }

  get fullTitle(): string {
    if (this.hasParent) return `${this.parent!.name}: ${this.name}`
    return this.name
  }

  get title(): string {
    if (this.hasParent) return `${this.parent!.name}`
    return this.name
  }

  get subtitle(): string {
    if (this.hasParent) return `${this.name}`
    return ""
  }

  set subcards(cards: Card[]) {
    this._subcards = cards.map(c => c.id)
  }

  get subcards(): Card[] {
    return this._subcards.map((id: string) => Deck.lookup(id)!)
  }

  addSubCard(card: Card) {
    if (!this.hasSubCard(card)) {
      this._subcards.push(card.id)
      this._subcards.sort()
    }
  }

  hasSubCard(card: Card): boolean {
    return this._subcards.find(id => id == card.id) != undefined
  }

  get level(): number {
    return this._level
  }

  get maxLevel(): number {
    if (this.hasParent) {
      return this.parent!.levels
    }
    return this.levels
  }

  get levelString(): string {
    if (this.type == 'single') return '1'
    if (this.isParent && this.levels == this.subcards.length) {
      return this.levels.toFixed(0)
    } else if (this.isParent) {
      return `${this.subcards.length} / ${this.levels}`
    } /*else if (this.type == 'selection') {
      return ''
      //return String.fromCharCode(96 + this.level).toUpperCase()
    }*/
    return `${this.level} / ${this.maxLevel}`
  }

  getLevel(level: number): Card {
    if (this.type != "single" && level < this.subcards.length) {
      return this.subcards[level]
    }
    return this
  }

  get parent(): Card | undefined {
    return Deck.lookup(this._parent)
  }

  get hasParent(): boolean {
    return this._parent != undefined
  }

  get isParent(): boolean {
    return this.type != "single" && this.levels > 1
  }

  get family(): Family {
    return Family.familyMap()[this._family]
  }

  hasTag(tag: string): boolean {
    return this.family.id == tag || this.tags.find(t => t == tag) != undefined
  }

  hasAnyTag(tags: string[]): boolean {
    for (let tag of tags) {
      if (this.hasTag(tag)) return true
    }
    return false
  }

  equals(c: Card) {
    return this.id == c.id
  }

  copy(): Card {
    let card = new Card({
      id: this.id,
      name: this.name,
      thumb: this.thumb,
      description: this.description,
      transformation: this.transformation,
      subcards: this._subcards,
      levels: this.levels,
      level: this._level,
      type: this.type,
      hue: this.hue,
      artist: this.artist,
      family: this._family,
      parent: this._parent,
      tags: this.tags
    })
    return card
  }

  toJSON() {
    return serialize(this)
  }
}
