    <template v-else>
      <div class="round-counter">
        <h3 v-if="game.round.value > 0">{{ game.roundString }}</h3>
        <h1 v-else style="margin: 0; padding: 0;">Ditzy Dice</h1>
      </div>
      <div v-if="game.round.value > 0 && game.stageId !== 'bonus'" class="bonus-rolls">
        <h3>Bonus rolls: {{ game.state.bonusRolls.value }} / {{ game.state.bonusRolls.max }}</h3>
      </div>
      <div v-if="game.state.cardsToPick > 0" class="cards-to-pick">
        <h3>Picked {{ game.state.cardsPickedThisRound }} of {{ game.state.cardsToPick }} cards</h3>
      </div>
    </template>
    <div class="buttons">
      <template v-for="button in game.buttons" v-bind:key="button">
        <TextButton @click="button.fun()">
          <p style="margin: 0;">{{ button.label }}</p>
        </TextButton>
      </template>
    </div>
  </div>
</template>

<script lang="ts">
