    ValueMeter, TextButton, ChatView
  },
  props: {
    menu: Object,
    title: String
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    }
  },
  computed: {
    game: function (): Game {
      return this.menu.game
    }
  }
})
export default class DialogueStatusCard extends Vue {
  menu!: Menu
  title!: string
}
